<template>
    <div>
        <div class="mt-2">
            1D : {{ toFixed(summary1d.profitSum, 2) }}<small>$</small>, Trade
            Count : {{ summary1d.tradeCount }}
        </div>

        <v-data-table
            mobile-breakpoint="200"
            hide-default-footer
            :items-per-page="-1"
            :headers="historySummaryHeaders"
            :items="historySummaries"
            sort-by="profitSum"
            dense
        >
        </v-data-table>

        <v-data-table
            mobile-breakpoint="200"
            hide-default-footer
            :items-per-page="-1"
            :headers="historyHeaders"
            :items="histories"
            dense
        >
            <template v-slot:item.side="{ item }">
                <v-chip
                    v-show="item.side === 'Buy'"
                    class="pa-1"
                    color="green lighten-4"
                    x-small
                    >{{ item.side }}</v-chip
                >
                <v-chip
                    v-show="item.side === 'Sell'"
                    class="pa-1"
                    color="red lighten-4"
                    x-small
                    >{{ item.side }}</v-chip
                >
            </template>

            <template v-slot:item.shortSymbol="{ item }">
                <small>{{ item.shortSymbol }}</small>
            </template>
        </v-data-table>
    </div>
</template>

<script>
import HttpService from '@/services/HttpService'

export default {
    name: 'history',

    data: () => ({
        historyHeaders: [
            { text: '', value: 'shortSymbol' },
            { text: 'Side', value: 'side' },
            { text: 'Buy', value: 'buy_price' },
            { text: 'Sell', value: 'sell_price' },
            { text: 'profit', value: 'profit' },
            { text: 'date', value: 'newDate' },
        ],

        historySummaryHeaders: [
            { text: '', value: 'shortSymbol' },
            { text: 'Total', value: 'profitSum' },
            { text: 'Buy', value: 'buyProfitSum' },
            { text: 'Sell', value: 'sellProfitSum' },
        ],

        histories: [],
        historySummaries: [],
        profitSum: 0,

        summary1d: {
            profitSum: 0,
            tradeCount: 0,
        },

        summary: {
            buyCountTotal: 0,
            sellCountTotal: 0,
            profitSum: 0,
        },
    }),

    computed: {},

    watch: {},

    created() {
        this.init()
        this.getHistorySummary()
    },

    methods: {
        async init() {},

        async getHistorySummary() {
            this.summary1d.profitSum = 0
            this.summary1d.tradeCount = 0

            let historySummary = await HttpService.get(
                '/coin/bybit/tribot/histories/summary'
            )
            let tempHistorySummaryObj = {}

            for (const item of historySummary) {
                if (tempHistorySummaryObj[item.symbol] === undefined) {
                    tempHistorySummaryObj[item.symbol] = {
                        buyProfitSum: 0,
                        sellProfitSum: 0,
                    }
                }

                if (item.side === 'Buy') {
                    tempHistorySummaryObj[item.symbol].buyProfitSum +=
                        item.profit
                } else {
                    tempHistorySummaryObj[item.symbol].sellProfitSum +=
                        item.profit
                }

                this.summary1d.profitSum += item.profit
                this.summary1d.tradeCount++
            }

            this.historySummaries = []
            for (const key in tempHistorySummaryObj) {
                const buyProfitSum = this.toFixed(
                    tempHistorySummaryObj[key].buyProfitSum,
                    3
                )
                const sellProfitSum = this.toFixed(
                    tempHistorySummaryObj[key].sellProfitSum,
                    3
                )
                const profitSum = this.toFixed(
                    tempHistorySummaryObj[key].buyProfitSum +
                        tempHistorySummaryObj[key].sellProfitSum,
                    3
                )

                this.historySummaries.push({
                    shortSymbol: key,
                    profitSum: profitSum,
                    buyProfitSum: buyProfitSum,
                    sellProfitSum: sellProfitSum,
                })
            }
        },

        setHistories(histories) {
            let tempProfitSum = 0

            for (let item of histories) {
                tempProfitSum += item.profit
                item.shortSymbol = item.symbol.split('USDT')[0]
                item.newDate = this.date2str(
                    new Date(item.created_date),
                    'hh:mm'
                )

                if (item.side === 'Buy') {
                    this.summary.buyCountTotal++
                } else {
                    this.summary.sellCountTotal++
                }
            }
            this.summary.profitSum = tempProfitSum
            this.histories = histories
        },

        requestGet(uri) {
            // localStorage.setItem('mexRequestModel', JSON.stringify(this.mexRequestModel));
            // let header = { headers: { sessionKey: this.mexRequestModel.key } }

            return new Promise((resolve, reject) => {
                this.$http
                    .get(uri)
                    .then((result) => {
                        resolve(result.data)
                    })
                    .catch((error) => {
                        resolve()
                    })
            })
        },

        date2str(x, y) {
            var z = {
                M: x.getMonth() + 1,
                d: x.getDate(),
                h: x.getHours(),
                m: x.getMinutes(),
                s: x.getSeconds(),
            }
            y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
                return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
            })

            return y.replace(/(y+)/g, function (v) {
                return x.getFullYear().toString().slice(-v.length)
            })
        },

        toFixed: function (val, num) {
            return Number(Number(val).toFixed(num))
        },
    },
}
</script>
