<template>
<div>

	<v-card
		class="mx-auto mt-12"
		max-width="350"
		v-if="user.length"
	>
		<span>@{{user[0].telegram_id}}</span>
		<div class="mt-12">Bot list</div>
	</v-card>


	<v-card
		class="mx-auto"
		outlined
		rounded
		max-width="350"
	>
		<v-list rounded>
			<!-- <v-subheader>Bot list</v-subheader> -->
			<v-list-item-group
				color="primary"
			>
				<v-list-item
					v-for="(item, i) in user"
					:key="i"
					@click="clickBot(item)"
				>
					<v-list-item-icon>
						<v-icon v-text="icon"></v-icon>
					</v-list-item-icon>
					<v-list-item-content>
						<v-list-item-title>{{item.bot_name}}</v-list-item-title>
						<small class="grey--text lighten-1">{{item.exchange}} / {{item.email}}</small>
					</v-list-item-content>
				</v-list-item>

			</v-list-item-group>
		</v-list>
	</v-card>

</div>
</template>


<script>

import UserService from '@/services/UserService'

export default {

	data: () => ({
		label:'',
		a:'',
		b:'',
		c:'',
		init_sum:'',

		selectedItem: 1,
		user: [],
		icon: 'mdi-flask-round-bottom',
		items: [
			{ text: 'Real-Time', icon: 'mdi-flask-round-bottom' },
			{ text: 'Audience', icon: 'mdi-flask-round-bottom' },
			{ text: 'Conversions', icon: 'mdi-flask-round-bottom' },
		],

	}),

	created: function () {

		this.telegramId = this.$store.state.telegramId
		this.init()
	},

	methods: {

		async init () {
			this.user = await UserService.getUser(this.telegramId)
			console.log(this.user)

		},

		clickBot(item){
			if(item.bot_name === 'Tribot'){
				this.$router.push('/tribot')
			}else if(item.bot_name === 'Rangebot'){
				this.$router.push('/range')
			}else if(item.bot_name === 'Cscalper'){
				this.$router.push('/cscal')
			}
		},
	}

};
</script>
