<template>

<div>






<form>
    <v-text-field
      v-model="email"
      label="E-mail"
      required
    ></v-text-field>


    <v-btn
      class="mr-4"
      @click="submit"
    >
      submit
    </v-btn>

  </form>



    <v-list
      color="transparent"
    >

      <v-subheader inset>accounts</v-subheader>

      <v-list-item
        v-for="account in this.$store.state.accounts"
        :key="account.email"
      >
<!--         <v-list-item-avatar>
          <v-icon
            :class="file.color"
            dark
            v-text="file.icon"
          ></v-icon>
        </v-list-item-avatar> -->

        <v-list-item-content>
          <v-list-item-title v-text="account.email"></v-list-item-title>

          <!-- <v-list-item-subtitle v-text="file.subtitle"></v-list-item-subtitle> -->
        </v-list-item-content>

        <v-list-item-action>
          <v-btn icon @click="clickRemove(account)">
            <v-icon color="grey lighten-1">mdi-delete</v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </v-list>


    </div>

</template>

<script>

export default {
  name: 'account',

  components: {
  },

  props: {
  },

  data: () => ({
    email:''
  }),


    methods: {

      submit(){
        this.$store.state.accounts.push({email:this.email})
        this.$store.commit('accounts',this.$store.state.accounts)
      },

      clickRemove(account){
        for(const [index, item] of this.$store.state.accounts.entries()){
          if (item.email === account.email){
            console.log(index)
            this.$store.state.accounts.splice(index, 1)
            break
          }
        }
        this.$store.commit('accounts',this.$store.state.accounts)
      }


    }


};
</script>

