<template>

<div>

	<v-row>
		<v-col class="ma-6 text-right">
			<small>@{{telegramId}}</small>
		</v-col>
	</v-row>

	<div class="ml-2 text-caption">
		Bot list
	</div>


	<v-list shaped>
		<!-- <v-subheader>Bot list</v-subheader> -->
		<v-list-item-group
			color="primary"
		>
			<v-list-item
				v-for="(item, i) in user"
				:key="i"
				@click="clickBot(item)"
			>
				<v-list-item-icon class="mr-0">
					<v-icon small>mdi-flask-round-bottom</v-icon>
				</v-list-item-icon>
				<v-list-item-content class="">
					<v-list-item-title>{{item.bot_name}}</v-list-item-title>
					<small class="grey--text lighten-1">{{item.exchange}}/{{item.email}}</small>
				</v-list-item-content>
			</v-list-item>

		</v-list-item-group>
	</v-list>



</div>

</template>

<script>

import UserService from '@/services/UserService'

export default {
	name: 'NavigationDrawer',

	data: () => ({

		telegramId: null,
		user: null,
		items: [
			{
				id: '/tribot',
				name: 'Tribot',
				icon: 'mdi-flask-round-bottom',
			},
			{
				id: '/account',
				name: 'Account',
				icon: 'mdi-flask-round-bottom',
			},

		],
	}),

	created: function () {

		this.telegramId = this.$store.state.telegramId
		this.init()
	},

	methods: {

		async init () {
			this.user = await UserService.getUser(this.telegramId)

		},

		clickBot(item){
			if(item.bot_name === 'Tribot'){
				this.$router.push('/tribot')
			}else if(item.bot_name === 'Rangebot'){
				this.$router.push('/range')
			}
		},
	}


};
</script>

