<template>

<div>

    <div class="mt-2">Orders</div>
    <v-data-table
    mobile-breakpoint="200"
    hide-default-footer
    :items-per-page="-1"
    :headers="headers"
    :items="orders"
    sort-by="buy_price"
    sort-desc
    class="elevation-1"
    dense
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              small
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              New Item
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>



                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.symbol"
                      label="Symbol"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.buy_price"
                      label="Buy price"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.sell_price"
                      label="Sell price"
                    ></v-text-field>
                  </v-col>
                 
                </v-row>

                 <v-row class="mt-1 ml-0">Profit : {{editedItem.profit}}</v-row>


              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="closeDialog"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="clickSave"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDialog">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>


        <template v-slot:item.actions="{ item }">
            <v-icon
                small
                @click="deleteItem(item)"
            >
                mdi-delete
            </v-icon>
        </template>

        <template v-slot:item.shortSymbol="{ item }">
            <small>{{item.shortSymbol}}</small>
        </template>

        <template v-slot:item.profit="{ item }">
            <small>{{item.profit}}</small>
        </template>

        <template v-slot:item.buy_price="{ item }">
            {{item.buy_price}} 
            <v-chip
                v-show="item.buyStatus === 'requested' "
                class="pa-1"
                color="amber lighten-2"
                x-small
            ></v-chip>
            <v-chip
                v-show="item.buyStatus === 'contracted' "
                class="pa-1"
                color="green lighten-2"
                x-small
            ></v-chip>
        </template>

        <template v-slot:item.sell_price="{ item }">
            {{item.sell_price}} 
            <v-chip
                v-show="item.sellStatus === 'requested' "
                class="pa-1"
                color="amber lighten-2"
                x-small
            ></v-chip>
            <v-chip
                v-show="item.sellStatus === 'contracted' "
                class="pa-1"
                color="green lighten-2"
                x-small
            ></v-chip>
        </template>

    </v-data-table>

    <div class="mt-2">Postions</div>

    <v-data-table
        mobile-breakpoint="200"
        hide-default-footer
        :items-per-page="-1"
        :headers="postionHeaders"
        :items="positions"
        dense
    >
        <template v-slot:item.buy_price="{ item }">
            {{item.buy_price}} 
            <v-chip
                v-show="item.buyStatus === 'requested' "
                class="pa-1"
                color="amber lighten-2"
                x-small
          ></v-chip>
          <v-chip
              v-show="item.buyStatus === 'contracted' "
              class="pa-1"
              color="green lighten-2"
              x-small
          ></v-chip>
      </template>
      <template v-slot:item.sell_price="{ item }">
          {{item.sell_price}} 
          <v-chip
              v-show="item.sellStatus === 'requested' "
              class="pa-1"
              color="amber lighten-2"
              x-small
          ></v-chip>
          <v-chip
              v-show="item.sellStatus === 'contracted' "
              class="pa-1"
              color="green lighten-2"
              x-small
          ></v-chip>
      </template>
    </v-data-table>

    <div class="mt-2">Histories : {{profitSum.toFixed(2)}}<small>$</small></div>
    <v-data-table
    mobile-breakpoint="200"
    hide-default-footer
    :items-per-page="-1"
    :headers="historyHeaders"
    :items="histories"
    dense
    >
    </v-data-table>

</div>

</template>

<script>

  import Util from '../../Util'

  export default {
    name: 'Mulbot',

    data: () => ({
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: '', value: 'shortSymbol', },
        { text: 'Buy', value: 'buy_price' },
        { text: 'Sell', value: 'sell_price' },
        { text: '%', value: 'profit' },
        { text: 'D', value: 'actions', sortable: false },
      ],

      postionHeaders: [
        { text: '', value: 'shortSymbol', },
        { text: 'Buy', value: 'buy_price' },
        { text: 'Sell', value: 'sell_price' },
        { text: '%', value: 'profit' },
      ],

      historyHeaders: [
        { text: '', value: 'shortSymbol', },
        { text: 'Buy', value: 'buy_price' },
        { text: 'Sell', value: 'sell_price' },
        { text: 'profit', value: 'profit' },
        { text: 'date', value: 'newDate' },
      ],

      desserts: [],
      editedIndex: -1,
      editedItem: {
        symbol: 'BTCUSDT',
        buy_price: '',
        sell_price: '',
      },

      orders:[],
      positions:[],
      histories:[],
      selected_item:{},
      profitSum :0,


    }),


    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch:{
        editedItem: {
            deep: true,
            handler(){
                this.editedItem.profit = ((this.editedItem.sell_price - this.editedItem.buy_price) / this.editedItem.buy_price * 100).toFixed(2) + '%'
            }
        }
    } ,

    created () {
      // this.initialize()
      this.getOrderList()
      this.getHistories()
    },


    methods: {

      async getOrderList () {

        let orders = await this.requestGet(this.$server+'/coin/bybit/feebot/orders')
        let positions = await this.requestGet(this.$server+'/coin/bybit/feebot/positions')

        for(let order of orders){
            for(let position of positions){
                if(order.buy_price === position.buy_price){
                    order.buyStatus = 'requested'
                    if(position.sell_order_id !== null){
                        order.buyStatus = 'contracted'
                        order.sellStatus = 'requested'
                    }
                }
            }
            order.shortSymbol = order.symbol.split('USDT')[0]
            order.profit =  ((order.sell_price - order.buy_price) / order.buy_price * 100).toFixed(2)
        }

            for(let position of positions){
                if(position.buy_price === position.buy_price){
                    position.buyStatus = 'requested'
                    if(position.sell_order_id !== null){
                        position.buyStatus = 'contracted'
                        position.sellStatus = 'requested'
                    }
                }
                position.shortSymbol = position.symbol.split('USDT')[0]
                position.profit =  ((position.sell_price - position.buy_price) / position.buy_price * 100).toFixed(2)
            }
            

        this.orders = orders
        this.positions = positions
      },

      async getHistories () {
        let histories = await this.requestGet(this.$server+'/coin/bybit/feebot/histories')

        for(let item of histories){
            this.profitSum+=item.profit
            item.shortSymbol = item.symbol.split('USDT')[0]
            // item.profit =  ((item.sell_price - item.buy_price) / item.buy_price * 100).toFixed(2)
            item.newDate = this.date2str(new Date(item.created_date), 'hh:mm,  MM/dd ')
        }

        this.histories = histories
      },


      date2str(x, y) {
    var z = {
        M: x.getMonth() + 1,
        d: x.getDate(),
        h: x.getHours(),
        m: x.getMinutes(),
        s: x.getSeconds()
    };
    y = y.replace(/(M+|d+|h+|m+|s+)/g, function(v) {
        return ((v.length > 1 ? "0" : "") + z[v.slice(-1)]).slice(-2)
    });

    return y.replace(/(y+)/g, function(v) {
        return x.getFullYear().toString().slice(-v.length)
    });
},



      deleteItem (item) {
        this.selected_item = item
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.deleteOrderList(this.selected_item)
        this.closeDialog()
      },

      closeDialog () {
        this.dialog = false
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedIndex = -1
        })
      },

      clickSave () {
        // if (this.editedIndex > -1) {
        //   Object.assign(this.desserts[this.editedIndex], this.editedItem)
        // } else {
        //   this.desserts.push(this.editedItem)
        // }

        console.log('click save')
        console.log(this.editedItem)

        this.postOrderList(this.editedItem)

        this.closeDialog()
      },



        requestGet(uri){
            // localStorage.setItem('mexRequestModel', JSON.stringify(this.mexRequestModel));
            // let header = { headers: { sessionKey: this.mexRequestModel.key } }

            return new Promise((resolve, reject) => {
            this.$http.get(uri)
                .then((result) => {
                    resolve(result.data)
                }).catch(error => {
                  resolve()
                })
            })
        },


      postOrderList (body) {
        this.$http.post(this.$server+'/coin/bybit/feebot/orders', body)
        .then((result) => {
          this.getOrderList()
        })
        .catch(error => {
          console.log('error'+error)
        });
      },

      deleteOrderList (item) {
        this.$http.delete(this.$server+'/coin/bybit/feebot/orders/' + item.id)
        .then((result) => {
          this.getOrderList()
        })
        .catch(error => {
          console.log('error'+error)
        });
      },

      test(){
      },

      toFixed:function(val, num){
        return Number(Number(val).toFixed(num))
      },

    },

}





</script>
