
import axios from 'axios'

export default new class {

    get (uri){
        return new Promise((resolve, reject) => {
            axios.get(process.env.VUE_APP_API+uri).then((result) => {
                resolve(result.data)
            }).catch(error => {
              resolve(null)
            })
        })
    }

    post (uri, body) {
        return new Promise((resolve, reject) => {
            axios.post(process.env.VUE_APP_API+uri, body).then((result) => {
                resolve(result.data)
            }).catch(error => {
                resolve(null)
            })
        })
    }

    put (uri, body) {

        console.log(process.env.VUE_APP_API+uri)

        return new Promise((resolve, reject) => {
            axios.put(process.env.VUE_APP_API+uri, body).then((result) => {
                resolve(result.data)
            }).catch(error => {
                resolve(null)
            })
        })
    }

    delete (uri) {
        return new Promise((resolve, reject) => {
            axios.delete(process.env.VUE_APP_API+uri).then((result) => {
                resolve(result.data)
            }).catch(error => {
                resolve(null)
            })
        })
    }


}

