<template>
	<div>
		<v-card class="mx-auto mt-12" max-width="500">
			<v-icon small>mdi-pine-tree</v-icon>Tribot
		</v-card>

		<v-card class="mx-auto" outlined rounded max-width="500">
			<v-data-table
				mobile-breakpoint="200"
				hide-default-footer
				:items-per-page="-1"
				:headers="historyHeaders"
				:items="histories"
				dense
			>
				<template v-slot:item.side="{ item }">
					<v-chip
						v-show="item.side === 'Buy'"
						class="pa-1"
						color="green lighten-4"
						x-small
						>{{ item.side }}</v-chip
					>
					<v-chip
						v-show="item.side === 'Sell'"
						class="pa-1"
						color="red lighten-4"
						x-small
						>{{ item.side }}</v-chip
					>
				</template>

				<template v-slot:item.shortSymbol="{ item }">
					<small>{{ item.shortSymbol }}</small>
				</template>
			</v-data-table>
		</v-card>
	</div>
</template>

<script>
import UserService from '@/services/UserService'
import HttpService from '@/services/HttpService'

export default {
	data: () => ({
		label: '',
		a: '',
		b: '',
		c: '',
		init_sum: '',

		selectedItem: 1,
		user: [],
		icon: 'mdi-flask-round-bottom',
		items: [
			{ text: 'Real-Time', icon: 'mdi-flask-round-bottom' },
			{ text: 'Audience', icon: 'mdi-flask-round-bottom' },
			{ text: 'Conversions', icon: 'mdi-flask-round-bottom' },
		],

		historyHeaders: [
			{ text: '', value: 'shortSymbol' },
			{ text: 'Side', value: 'side' },
			{ text: 'Buy', value: 'buy_price' },
			{ text: 'Sell', value: 'sell_price' },
			// { text: 'profit', value: 'profit' },
			{ text: 'date', value: 'newDate' },
		],

		histories: [],
	}),

	created: function () {
		this.telegramId = this.$store.state.telegramId
		this.init()
		this.getHistories()
	},

	methods: {
		async init() {
			this.user = await UserService.getUser(this.telegramId)
			console.log(this.user)
		},

		async getHistories() {
			let histories = await HttpService.get(
				'/coin/bybit/tribot/histories'
			)

			let tempProfitSum = 0

			for (let item of histories) {
				tempProfitSum += item.profit
				item.shortSymbol = item.symbol.split('USDT')[0]
				// item.profit =  ((item.sell_price - item.buy_price) / item.buy_price * 100).toFixed(2)
				item.newDate = this.date2str(
					new Date(item.created_date),
					'hh:mm'
				)

				let time = new Date(item.created_date).getTime()
			}

			this.histories = histories
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		clickBot(item) {
			if (item.bot_name === 'Tribot') {
				this.$router.push('/tribot')
			} else if (item.bot_name === 'Rangebot') {
				this.$router.push('/range')
			} else if (item.bot_name === 'Cscalper') {
				this.$router.push('/cscal')
			}
		},
	},
}
</script>
