<script>
import Util from '../../Util'
import Histories from './Histories'

import HttpService from '@/services/HttpService'

export default {
	name: 'Mulbot',

	components: {
		Histories,
	},

	data: () => ({
		dialog: false,
		dialogDelete: false,
		orderHeaders: [
			{ text: '', value: 'shortSymbol', width: 45 },
			{ text: 'Side', value: 'side', width: 32 },
			{
				text: 'Buy',
				value: 'buy_price',
				width: 100,
			},
			{
				text: 'Sell',
				value: 'sell_price',
				width: 100,
			},
			{ text: 'P', value: 'price', width: 63 },
			{ text: 'Amt', value: 'amt' },
			{ text: 'Id', value: 'id' },
			{ text: '%', value: 'profit' },
		],

		postionHeaders: [
			{ text: '', value: 'shortSymbol' },
			{ text: 'Id', value: 'oi' },
			{ text: 'Buy', value: 'bp' },
			{ text: 'Sell', value: 'sp' },
		],

		historyHeaders: [
			{ text: '', value: 'shortSymbol' },
			{ text: 'Side', value: 'side' },
			{ text: 'Buy', value: 'buy_price' },
			{ text: 'Sell', value: 'sell_price' },
			{ text: 'profit', value: 'profit' },
			{ text: 'date', value: 'newDate' },
		],

		summaryListHeaders: [
			{ text: '', value: 'shortSymbol' },
			{ text: 'Pending', value: 'pendingCount' },
			{ text: 'Created', value: 'createdCount' },
			{ text: 'Filled', value: 'total' },
		],

		expanded: [],
		editedIndex: -1,
		editedItem: {
			symbol: '',
			buy_price: '',
			sell_price: '',
		},
		orders: [],
		positions: [],
		originalPositions: [],
		histories: [],
		selected_item: {},
		profitSum: 0,
		searchOrderText: 'aaa',

		showPositionTable: false,

		showSnackBar: false,
		showHistory: false,
		showOrder: true,
		mode: null,

		itemDialog: {
			show: false,
			mode: null,
			title: '',
		},

		summaryText: {
			buyPriceTotal: 0,
			sellPriceTotal: 0,
			buyCountTotal: 0,
			sellCountTotal: 0,
			profitSum: 0,
		},

		summaryList: [],
		tempLast3h: {},
	}),

	computed: {
		formTitle() {
			return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
		},
	},

	watch: {},

	created() {
		// this.initialize()
		this.getOrderList()

		let contracts = 163476
		let price = 0.7429

		let targetPrice = 10

		let purePrice = contracts * price
		let divider = purePrice / targetPrice

		console.log(purePrice)
		console.log(divider)

		let actualAmt = (contracts / divider).toFixed(0)

		console.log(actualAmt)
		console.log(actualAmt * price)
	},

	methods: {
		async getOrderList() {
			this.summaryText.buyPriceTotal = 0
			this.summaryText.sellPriceTotal = 0
			this.summaryText.buyCountTotal = 0
			this.summaryText.sellCountTotal = 0

			let orders = await HttpService.get('/coin/bybit/tribot/orders')
			let positions = await HttpService.get(
				'/coin/bybit/tribot/positions'
			)

			this.originalPositions = positions

			this.tempLast3h = {}
			this.summaryList = []

			for (let order of orders) {
				if (this.tempLast3h[order.symbol] === undefined) {
					this.tempLast3h[order.symbol] = {
						buyCount: 0,
						sellCount: 0,
						createdCount: 0,
						longCreatedCount: 0,
						shortCreatedCount: 0,
						pendingCount: 0,
						longPendingCount: 0,
						shortPendingCount: 0,
					}
				}

				let isFindPosition = false

				for (let position of positions) {
					if (order.id === position.oi) {
						order.bs = position.bs
						order.buy_price = position.bp
						order.ss = position.ss
						order.sell_price = position.sp
						order.profit =
							((order.sell_price - order.buy_price) /
								order.buy_price) *
							100
						order.profit = this.toFixed(order.profit, 2)

						order.price = order.buy_price * order.amt
						order.price = this.toFixed(order.price, 2)

						if (order.side === 'Buy') {
							this.summaryText.buyPriceTotal += order.price
						} else if (order.side === 'Sell') {
							this.summaryText.sellPriceTotal += order.price
						}

						if (order.bs === 'New' || order.bs === 'Created') {
							this.tempLast3h[order.symbol].createdCount++
							if (order.side === 'Buy') {
								this.tempLast3h[order.symbol].longCreatedCount++
							} else if (order.side === 'Sell') {
								this.tempLast3h[order.symbol]
									.shortCreatedCount++
							}
						}
						isFindPosition = true
						break
					}
				}

				if (!isFindPosition) {
					this.tempLast3h[order.symbol].pendingCount++
					if (order.side === 'Buy') {
						this.tempLast3h[order.symbol].longPendingCount++
					} else if (order.side === 'Sell') {
						this.tempLast3h[order.symbol].shortPendingCount++
					}
				}

				order.shortSymbol = order.symbol.split('USDT')[0]
			}

			const ticker = await HttpService.get('/coin/bybit/tribot/ticker')

			for (const key in ticker) {
				const shortSymbol = key.split('USDT')[0]
				orders.push({
					symbol: key,
					shortSymbol: shortSymbol,
					bs: 'Ticker',
					buy_price: ticker[key].price,
					side: '---',
				})
			}

			this.orders = orders

			let tempPositions = []
			for (let position of positions) {
				let isFind = false

				for (let order of orders) {
					if (order.id === position.oi) {
						isFind = true
						break
					}
				}

				if (!isFind) {
					position.shortSymbol = position.symbol.split('USDT')[0]
					position.profit = (
						((position.sell_price - position.buy_price) /
							position.buy_price) *
						100
					).toFixed(2)
					tempPositions.push(position)
				}
			}

			for (let key in this.tempLast3h) {
				const shortSymbol = key.split('USDT')[0]
				this.summaryList.push({
					symbol: key,
					shortSymbol: shortSymbol,
					total: 0,
					createdCount: this.tempLast3h[key].createdCount,
					pendingCount: this.tempLast3h[key].pendingCount,
					longPendingCount: this.tempLast3h[key].longPendingCount,
					shortPendingCount: this.tempLast3h[key].shortPendingCount,
					longCreatedCount: this.tempLast3h[key].longCreatedCount,
					shortCreatedCount: this.tempLast3h[key].shortCreatedCount,
				})
			}

			this.positions = tempPositions
			this.getHistories()
		},

		async updatePosition() {
			this.summaryText.buyPriceTotal = 0
			this.summaryText.sellPriceTotal = 0
			this.summaryText.buyCountTotal = 0
			this.summaryText.sellCountTotal = 0

			let orders = await HttpService.get('/coin/bybit/tribot/orders')
			let positions = await HttpService.get(
				'/coin/bybit/tribot/positions'
			)

			let tempSummary = {}
			let last3h = []

			for (let order of orders) {
				if (tempSummary[order.symbol] === undefined) {
					tempSummary[order.symbol] = {
						buyCount: 0,
						sellCount: 0,
						createdCount: 0,
						pendingCount: 0,
						longCreatedCount: 0,
						shortCreatedCount: 0,
						longPendingCount: 0,
						shortPendingCount: 0,
					}
				}

				let isFindPosition = false

				for (let position of positions) {
					if (order.id === position.oi) {
						order.bs = position.bs
						order.buy_price = position.bp
						order.ss = position.ss
						order.sell_price = position.sp
						order.profit =
							((order.sell_price - order.buy_price) /
								order.buy_price) *
							100
						order.profit = this.toFixed(order.profit, 2)

						order.price = order.buy_price * order.amt
						order.price = this.toFixed(order.price, 2)

						if (order.bs === 'New' || order.bs === 'Created') {
							tempSummary[order.symbol].createdCount++
							if (order.side === 'Buy') {
								tempSummary[order.symbol].longCreatedCount++
							} else if (order.side === 'Sell') {
								tempSummary[order.symbol].shortCreatedCount++
							}
						}

						isFindPosition = true
						break
					}
				}

				if (!isFindPosition) {
					tempSummary[order.symbol].pendingCount++
					if (order.side === 'Buy') {
						tempSummary[order.symbol].longPendingCount++
					} else if (order.side === 'Sell') {
						tempSummary[order.symbol].shortPendingCount++
					}
				}

				order.shortSymbol = order.symbol.split('USDT')[0]
			}

			const ticker = await HttpService.get('/coin/bybit/tribot/ticker')

			for (const key in ticker) {
				const shortSymbol = key.split('USDT')[0]
				orders.push({
					symbol: key,
					shortSymbol: shortSymbol,
					bs: 'Ticker',
					buy_price: ticker[key].price,
					side: '---',
				})
			}

			this.orders = orders

			for (let key in tempSummary) {
				const shortSymbol = key.split('USDT')[0]
				last3h.push({
					symbol: key,
					shortSymbol: shortSymbol,
					total: 0,
					createdCount: tempSummary[key].createdCount,
					pendingCount: tempSummary[key].pendingCount,
					longPendingCount: tempSummary[key].longPendingCount,
					shortPendingCount: tempSummary[key].shortPendingCount,
					longCreatedCount: tempSummary[key].longCreatedCount,
					shortCreatedCount: tempSummary[key].shortCreatedCount,
				})
			}

			this.summaryList = last3h
		},

		async getHistories() {
			let histories = await HttpService.get(
				'/coin/bybit/tribot/histories'
			)

			let tempProfitSum = 0

			for (let item of histories) {
				tempProfitSum += item.profit
				item.shortSymbol = item.symbol.split('USDT')[0]
				// item.profit =  ((item.sell_price - item.buy_price) / item.buy_price * 100).toFixed(2)
				item.newDate = this.date2str(
					new Date(item.created_date),
					'hh:mm'
				)
				this.summaryText.lastTime = item.newDate

				let time = new Date(item.created_date).getTime()

				if (item.side === 'Buy') {
					this.tempLast3h[item.symbol].buyCount++
				} else {
					this.tempLast3h[item.symbol].sellCount++
				}

				if (item.side === 'Buy') {
					this.summaryText.buyCountTotal++
				} else {
					this.summaryText.sellCountTotal++
				}
			}
			this.summaryText.profitSum = tempProfitSum

			// this.histories = histories
			this.$refs.historyView.setHistories(histories)

			for (const item of this.summaryList) {
				item.h3BuyCount = this.tempLast3h[item.symbol].buyCount
				item.h3SellCount = this.tempLast3h[item.symbol].sellCount
				item.total = item.h3BuyCount + item.h3SellCount
			}
		},

		priceColor(item) {
			let color = ''
			if (item === 'Created') {
				color = 'amber lighten-4'
			} else if (item === 'New') {
				color = 'amber lighten-2'
			} else if (item === 'Filled') {
				color = 'blue lighten-3'
			} else if (item === 'PartiallyFilled') {
				color = 'blue lighten-4'
			} else if (item === 'Cancelled') {
				color = 'red lighten-2'
			} else if (item === 'Ticker') {
				color = 'transparent'
			} else {
				color = 'transparent'
			}
			return color
		},

		sideColor(side) {
			let color = ''
			if (side === 'Buy') {
				color = 'green lighten-4'
			} else if (side === 'Sell') {
				color = 'red lighten-4'
			} else {
				color = 'white'
			}
			return color
		},

		clickSummaryRow(row) {
			if (
				this.expanded.length &&
				row.symbol === this.expanded[0].symbol
			) {
				this.expanded = []
			} else {
				this.expanded = [row]
			}

			this.searchOrderText = row.shortSymbol

			console.log(this.expanded)
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		clickOrderRow(row) {
			this.showSnackBar = true
			this.editedItem = row

			// console.log(row)

			this.editedItem.sell_percent = row.spt
			this.editedItem.buy_percent = row.bpt
			// this.editedItem.realPrice =
		},

		clickSnackbarBtn(mode) {
			if (mode === 'New') {
				this.itemDialog.title = 'New Item'
			} else if (mode === 'Edit') {
				this.itemDialog.title = 'Edit Item'
			} else if (mode === 'Cancel') {
				this.itemDialog.title = 'Cancel Item'
			}

			this.itemDialog.show = true
			this.itemDialog.mode = mode
			this.showSnackBar = false
		},

		async clickDialogBtn() {
			if (this.itemDialog.mode === 'New') {
				await HttpService.post(
					'/coin/bybit/tribot/orders',
					this.editedItem
				)
				this.updatePosition()
			} else if (this.itemDialog.mode === 'Edit') {
				await HttpService.put(
					'/coin/bybit/tribot/orders',
					this.editedItem
				)
				this.updatePosition()
			} else if (this.itemDialog.mode === 'Cancel') {
				await HttpService.delete(
					'/coin/bybit/tribot/cancel/orders/' + this.editedItem.id
				)
				if (this.editedItem.bs === 'New') {
					this.editedItem.bs = 'CancelRequested'
				} else {
					this.updatePosition()
				}
			}
			this.closeDialog()
		},

		closeDialog() {
			this.itemDialog.show = false
			this.dialogDelete = false
			this.$nextTick(() => {
				this.editedIndex = -1
			})
		},

		async clickUpdatePercent() {
			await HttpService.put(
				'/coin/bybit/tribot/orders/percent',
				this.editedItem
			)
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>

