
import axios from 'axios'

export default new class {

  getUser (telegramId) {
    return new Promise((resolve, reject) => {
      axios.get(`${process.env.VUE_APP_API}/pish/telegram/users/${telegramId}`).then((result) => {
        resolve(result.data)
      }).catch(error => {
        resolve(null)
      })
    })
  }

  getTest () {

  }


}

