<template>

<div>

    <v-card class="mx-auto pa-6 pt-10 mt-12" width="350">

        <v-row v-if="signInResult === null">
            <v-col cols="8" class="pr-0">
                <v-text-field
                    label="Telegram Id"
                    v-model="telegramId"
                    outlined
                    dense
                ></v-text-field>
            </v-col>
            <v-col cols="4" class="">
                <v-btn
                    
                    class=""
                    color="primary"
                    @click="clickSignin"
                >
                    signin
                </v-btn>
            </v-col>
        </v-row>

        <v-row v-if="signInResult !== null">
            <v-col cols="8" class="pr-0">
                <v-text-field
                    label="CODE"
                    v-model="code"
                    outlined
                    dense
                ></v-text-field>
            </v-col>
            <v-col cols="4" class="">
                <v-btn
                    class=""
                    color="primary"
                    @click="clickSubmit"
                >
                    submit
                </v-btn>
            </v-col>
        </v-row>

    </v-card>

</div>

</template>

<script>

export default {
    name: 'signin',

    data: () => ({
        telegramId: null,
        signInResult: null,
        code:null
    }),

    methods: {

        clickSignin(){
            console.log(process.env.VUE_APP_API)
            this.$http.post(process.env.VUE_APP_API+'/pish/telegram/signin', {telegramId: this.telegramId}).then((result) => {
                // console.log(result.data)
                this.signInResult = result.data
            }).catch(error => {
                console.log('error'+error)
            });

        },

        clickSubmit(){
            if(this.code === this.signInResult.code){
                this.$store.commit('telegramId', this.signInResult.telegramId)
                this.$router.push('/')
            }
        }


    }


};
</script>

