<template>

  <v-card
    class="mx-auto"
    max-width="500"
    style="touch-action: manipulation;"
  >
<v-container fluid>

<v-card
    class="mx-auto"
    outlined
  >

  <v-row
      v-if="coins.length"
      no-gutters
      class="mt-1 text-center"
    >
      <v-col
        class="ml-2 mr-2"
      >
          <v-text-field
            v-model="coins[0].price_add_value"
            label="atr*"
            @change="changeTextField('price_add_value', coins[0].price_add_value)"
          ></v-text-field>
      </v-col>

      <v-col
        class="ml-2 mr-2"
      >
          <v-text-field
            v-model="coins[0].limit_usd"
            label="limit"
          ></v-text-field>
      </v-col>

      <v-col
        class="ml-2 mr-2"
      >
        <v-text-field
            v-model="coins[0].sell_percent"
            label="sell percent"
          ></v-text-field>
      </v-col>

      <v-col
        class="ml-2 mr-2"
      >
        <v-text-field
            v-model="coins[0].mul_percent"
            label="mulpercent"
          ></v-text-field>
      </v-col>
    </v-row>

</v-card>


<v-row no-gutters>
  

    <v-row
      no-gutters
      class="mt-1 mb-3"
    >
      <v-col
        class="ml-1"
        cols="1"
      >
      </v-col>
      <v-col
        class=""
        cols="2"
        @click="sortClick('rank')"
      >
        <small>RANK</small>
      </v-col>

      <v-col
        class="text-right ml-2"
        cols="2"
        @click="sortClick('price')"
      >
        <small>PRICE</small>
      </v-col>

      <v-col
        class="text-right"
        cols="4"
        @click="sortClick('percent')"
      >
        <small>PERCENT</small>
      </v-col>

      <v-col
        class="text-right"
        cols="2"
        @click="sortClick('init')"
      >
        <small>INIT</small>
      </v-col>
    </v-row>

    <v-expansion-panels accordion>
      <v-expansion-panel
        v-for="(item,i) in coins"
        :key="i"
      >
        <v-expansion-panel-header
         hide-actions
         class="pa-0 pl-3 mb-n1 mt-n1"
         >

    <v-row
      no-gutters
      class=""
    >
      <v-col
        class=""
        cols="1"
      >
          <v-img
            :src="getImage(item.symbol2)"
            class=""
            max-width="16"
            max-height="16"
            @error="test"
          ></v-img>
          
      </v-col>
      <v-col
        class="ml-n1"
        cols="2"
      >
      <small class="grey--text text--darken-1">{{item.symbol2}}</small><small style="font-size: 0.6rem" > {{item.rank}}</small>
      </v-col>

      <v-col
        class="ml-3 text-right"
        cols="2"
      >
        <span class="grey--text" style="font-size:0.6rem">$</span>{{item.usd_position_amt}}
      </v-col>

      <v-col
        class="text-right"
        cols="4"
      >
      {{item.percent}}<small class="grey--text text--darken-1">% ({{item.buy_percent}})</small>
      </v-col>

      <v-col
        class="text-right"
        cols="2"
      >
      <small><span class="grey--text" style="font-size:0.5rem">$</span>{{item.actual_price}}</small>
      </v-col>



    </v-row>

      </v-expansion-panel-header>
        <v-expansion-panel-content >

<v-row no-gutters>

<v-col>
            earn : {{item.earn}}

</v-col>  



<v-col>
  

<v-card outlined>
  

          amt : {{item.init_amt}}<br/>

   <v-btn
      class="mx-2 mt-1 mb-1"
      small
      fab
      outlined
      @click="clickAmt(item, 'plus')"
    >
      <v-icon>
        mdi-plus
      </v-icon>
    </v-btn>

   <v-btn
      class="mx-2 mt-1 mb-1"
      small
      fab
      outlined
      @click="clickAmt(item, 'minus')"
    >
      <v-icon >
        mdi-minus
      </v-icon>
    </v-btn>


</v-card>

</v-col>  


</v-row>




        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-row>


</v-container>

  </v-card>


</template>

<script>

  import Util from '../../Util'

  export default {
    name: 'Mulbot',

    data: () => ({

      ticker:{},
      coins:[],
      sort:{rank:false, price:false, percent:false, init:false}

    }),

    watch:{
    } ,


    created: function () {
      console.log('created!!')
      this.getTicker()

    },


    methods: {

        getTicker: function () {
            this.$http.get(this.$server+'/coin/ticker/all')
            .then((result) => {
                  this.ticker = result.data
                  this.getCoins()
            })
            .catch(error => {
              console.log('error'+error)
            });
        },

          getCoins:function(){
            let header = { headers: { sessionKey: 'nfftzo@naver.com' } }
            this.$http.get(this.$server +'/coin/binance/bot/mul',header)
            .then((result) => {
                // console.log(result)
                this.sumUsd = 0
                this.sumInitAmtUsd = 0
                for(let item of this.ticker.binance){
                    for(let botItem of result.data){
                        if(item.symbol === botItem.symbol){
                            botItem.lastPrice = item.lastPrice
                            botItem.actual_price = Number((botItem.init_amt * item.lastPrice).toFixed(1))
                            botItem.quoteVolume = Number(Number(item.quoteVolume).toFixed(0))
                            botItem.symbol2 = botItem.symbol.replace('USDT','')
                            botItem.symbolLower = botItem.symbol2.toLowerCase()

                            let find = Util.getObject(this.ticker.coingecko,'symbol',botItem.symbol2.toLowerCase())[0]
                            if(find!==undefined){
                                botItem.h1 = this.toFixed(find.h1, 1)
                                botItem.h24 = this.toFixed(find.h24, 1)
                            }else{
                                botItem.h1 = 0
                                botItem.h24 = 0
                            }

                            botItem.usd_position_amt = Number(botItem.usd_position_amt.toFixed(0))
                            this.sumUsd += botItem.usd_position_amt
                            this.sumInitAmtUsd +=botItem.actual_price
                            botItem.high_usd_price = Number(botItem.high_usd_price.toFixed(0))
                            botItem.percent = Number(botItem.percent.toFixed(1))
                            
                            botItem.items = []

                        }
                    }
                }

                this.sumInitAmtUsd = this.sumInitAmtUsd.toFixed(0)

                result.data.sort(function(a, b) {
                    return b.usd_position_amt - a.usd_position_amt;
                });

                this.coins = result.data
            })
            .catch(error => {
                console.log('getcoinerrror'+error)
            });
        },


        sortClick(field){

          if(field ==='rank'){

            if(!this.sort.rank){
              this.coins.sort(function(a, b) {
                return a.rank - b.rank;
              });
            }else{
              this.coins.sort(function(a, b) {
                return b.rank - a.rank;
              });
            }
            this.sort.rank = !this.sort.rank
          }else if(field ==='price'){
            if(!this.sort.price){
              this.coins.sort(function(a, b) {
                return b.usd_position_amt - a.usd_position_amt;
              });
            }else{
              this.coins.sort(function(a, b) {
                return a.usd_position_amt - b.usd_position_amt;
              });
            }
            this.sort.price = !this.sort.price
          }else if(field ==='percent'){
            if(!this.sort.percent){
              this.coins.sort(function(a, b) {
                return b.percent - a.percent;
              });
            }else{
              this.coins.sort(function(a, b) {
                return a.percent - b.percent;
              });
            }
            this.sort.percent = !this.sort.percent
          }else if(field ==='init'){
            if(!this.sort.init){
              this.coins.sort(function(a, b) {
                return b.actual_price - a.actual_price;
              });
            }else{
              this.coins.sort(function(a, b) {
                return a.actual_price - b.actual_price;
              });
            }
            this.sort.init = !this.sort.init
          }




          console.log(field)
        },

        changeTextField(field, value){
          // if(field === 'price_add_value'){

          // }

          console.log(value)
        },

        clickAmt(item, value){

          let addValue = 1

          if(item.fixed === 1){
            addValue = 0.1
          }else if(item.fixed === 2){
            addValue = 0.01
          }else if(item.fixed === 3){
            addValue = 0.001
          } 

          if (value === 'plus'){
            item.init_amt = item.init_amt + addValue
          }else if(value === 'minus'){
            item.init_amt = item.init_amt - addValue
          }

          item.init_amt = Number(item.init_amt.toFixed(item.fixed))

          item.actual_price = Number((item.init_amt * item.lastPrice).toFixed(1))


          console.log('click : ' + item.init_amt)
          console.log(item)
        },

        getImage(symbol){
          try {
            symbol = symbol.toLowerCase()
            console.log(`@/assets/coins/${symbol}.png`)
            return require(`@/assets/coins/${symbol}.png`)
          } catch (e) {
            return ''
          }
        },

        test(){

        },

        toFixed:function(val, num){
          return Number(Number(val).toFixed(num))
        },

    },

  }
</script>
